var $, Cell, Immutable, Mamba, ROOT, Snake, _, game_over, keyhandler, position, ref, renderer, settings,
  bind = function(fn, me){ return function(){ return fn.apply(me, arguments); }; };

Snake = require('./snake');

Cell = require('./cell');

settings = require('./settings');

ref = require('./util'), keyhandler = ref.keyhandler, renderer = ref.renderer, position = ref.position, game_over = ref.game_over;

_ = require('underscore');

Immutable = require('immutable');

$ = window.$;

Mamba = (function() {

  /*
    Has-one Renderer
    Has-one Keyhandler
   */
  Mamba.motion_keys = {
    37: position.value_of(0, -1),
    38: position.value_of(-1, 0),
    39: position.value_of(0, +1),
    40: position.value_of(+1, 0)
  };

  Mamba.method_keys = {
    82: '__restart'
  };

  function Mamba(grid_node) {
    this._on_smash = bind(this._on_smash, this);
    this._on_reset = bind(this._on_reset, this);
    this._renderloop_hook = bind(this._renderloop_hook, this);
    this._keyup = bind(this._keyup, this);
    this._game_over = null;
    this._reset_snake();
    this._keyhandler = keyhandler.from_handler(this._keyup, $).handle(this.keycodes());
    this._renderer = renderer.mount(grid_node).render(this._renderprops({
      reset: true
    }));
  }

  Mamba.prototype.keycodes = function() {
    var method_keys, motion_keys;
    if (this._keycodes == null) {
      motion_keys = _.keys(this.constructor.motion_keys).map(parseFloat);
      method_keys = _.keys(this.constructor.method_keys).map(parseFloat);
      this._keycodes = Immutable.Set(method_keys.concat(motion_keys));
    }
    return this._keycodes;
  };

  Mamba.prototype._reset_snake = function() {
    return this._snake = Snake.at_position(position.random(settings.GRID.dimension - 1));
  };

  Mamba.prototype._keyup = function(keycode) {
    var method, motion;
    motion = this.constructor.motion_keys[keycode];
    method = this.constructor.method_keys[keycode];
    if (motion && (this._game_over == null)) {
      this._snake.motion(motion);
      return (!this._renderer.looping()) && this._renderer.loop(this._renderloop_hook, settings.RENDER.interval);
    } else if (method != null) {
      return this[method]();
    }
  };

  Mamba.prototype.__restart = function() {
    this._game_over = null;
    this._reset_snake();
    return this._renderer.update(this._renderprops({
      reset: true
    }), (function(_this) {
      return function() {
        return _this._renderer.stop();
      };
    })(this));
  };

  Mamba.prototype._renderloop_hook = function() {
    this._snake.move();
    return this._renderprops();
  };

  Mamba.prototype._renderprops = function(props) {
    if (props == null) {
      props = {};
    }
    return _.defaults(props, {
      reset: false,
      snake: this._snake,
      game_over: this._game_over,
      on_smash: this._on_smash,
      on_reset: this._on_reset
    });
  };

  Mamba.prototype._on_reset = function(_Items_left) {
    this._Items_left = _Items_left;
  };

  Mamba.prototype._on_smash = function(arg) {
    var smashed, smasher, xy;
    smashed = arg.smashed, smasher = arg.smasher, xy = arg.xy;
    if (smashed === Cell.Snake && smasher === Cell.Snake && this._snake.head() === xy) {
      return this._game_over_failure();
    } else if (smashed === Cell.Wall) {
      return this._game_over_failure();
    } else if (smashed === Cell.Item) {
      this._Items_left--;
      if (this._Items_left === 0) {
        return this._game_over_success();
      } else {
        return this._snake.grow();
      }
    }
  };

  Mamba.prototype._game_over_success = function() {
    this._game_over = game_over.success;
    return this._renderer.update(this._renderprops(), (function(_this) {
      return function() {
        return _this._renderer.stop();
      };
    })(this));
  };

  Mamba.prototype._game_over_failure = function() {
    this._game_over = game_over.failure;
    this._snake.motion(null);
    return this._renderer.stop(setTimeout((function(_this) {
      return function() {
        _this._snake.rewind();
        return _this._renderer.update(_this._renderprops());
      };
    })(this), settings.RENDER.interval));
  };

  return Mamba;

})();

if ($ != null) {
  ROOT = $('#mamba')[0];
  window.mamba = new Mamba(ROOT);
} else {
  throw new Error("Couldn't find window.$, are you sure jQuery is loaded?");
}
