var Grid, Renderer, _;

Grid = require('../grid');

_ = require('underscore');

Renderer = (function() {
  function Renderer(html_element1) {
    this.html_element = html_element1;
    Grid.html_element(this.html_element);
    this._interval = null;
  }

  Renderer.mount = function(html_element) {
    return new this(html_element);
  };

  Renderer.prototype.looping = function() {
    return this._interval != null;
  };

  Renderer.prototype.loop = function(props_returning_fn, milliseconds) {
    this.stop();
    this._interval = setInterval(_.compose(this.update, props_returning_fn), milliseconds);
    return this;
  };

  Renderer.prototype.render = function(props) {
    if (props == null) {
      props = {};
    }
    Grid.render(props);
    return this;
  };

  Renderer.prototype.update = function(props, callback) {
    if (props == null) {
      props = {};
    }
    Grid.set_props(props, callback);
    return this;
  };

  Renderer.prototype.stop = function(callback) {
    clearInterval(this._interval);
    this._interval = null;
    return typeof callback === "function" ? callback() : void 0;
  };

  return Renderer;

})();

module.exports = {
  mount: Renderer.mount.bind(Renderer)
};
